    p.mb-4.mt-4 El Centro de Estudios de Lectura y Escritura  CELEE de la Universidad EAFIT (s.f.) refiere que las preposiciones cumplen una funcin sintctica (modo en que se combinan las palabras): unir palabras o frases dentro de un enunciado. Tambin cumplen una funcin semntica (significado de las palabras) concretar el significado de la palabra siguiente con relacin a la anterior y sirven para indicar lugar, tiempo, destino, causa, etc.

